














































{ Query } = require "~plugins/memory-record"
_ = require "lodash"

module.exports =
  mixins: [
    require("~plugins/get-by-mount") "12h", "aggregate/faces"
    require("~plugins/browser-store")
      push:
        order: "date_max"
        tag_id:  "all"
  ]

  computed:
    faces: ->
      @read_at
      Query.faces.aggregate(@tag_id, @order)




